.footer-container{
    width: 100%;
    height: 100vh;
    background-image: url("../assets/footer-wallpaper.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 100% 100%;
    display: flex;
    justify-content: center;
    
}

@media only screen and (max-width: 600px) {
    .footer-container{
       margin: 0;
    }
}