@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
    


.navbar-container{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgba(200,53,255,255);
    width: 100%;
    height: 87px;
}
.navbar-logo{
    color: white;
    font-family: "Monda";
    font-size: 34px;
    margin-right: 15rem;
    margin-left: 2rem;
    cursor: default;
    align-self: center;
}
.navbar-links-wrapper{
    display: flex;
    justify-content: flex-end;
    width: 20rem;
    margin-left: 10rem;
    font-family: "Roboto", sans-serif;
    
}
.navbar-container img{
    width: 25px;
    height: 25px;
    margin-left: 0.3rem;
    margin-right: 0.3rem;
}

.navbar-item{
    margin: 0.7rem;
    outline: none;
    text-decoration: none;
    color: white;
    transition: 0.5s;
}

.navbar-socials{
    margin-right: 2rem;
    width: 10%;
    display: flex;
    justify-content: flex-end;
    filter: grayscale(100%);
    
}
.navbar-item:hover{
    color: rgb(207, 206, 206);
    scale: 1.1;
}
.navbar-icon-open{
    width: 25px;
    height: 25px;
    display: none;
    color: white;
    
}
.navbar-icon-close{
    position: fixed;
    right: 0;
    top: 0;
    margin: 3rem;
    width: 25px;
    height: 25px;
    transition: 0.5s;
    animation-delay: 0.3s;
}
.navbar-responsive{
    background-color: rgb(33, 33, 33);
    font-family: "Patua One", sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 5rem;
    flex-direction: column;
    position: fixed;
    top: 0;   
    z-index: 9999;
    height: 50%;
    width: 100%;
    transition: all 0.5s ease;
}

@media  only screen and ( max-width: 988px ) {
    .navbar-container{
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        width: 100%;
    }
    .navbar-logo{
        margin-right: 0;
        width: 20rem;
    }
    .navbar-links-wrapper{
        display: none;
        transition: all 0.5s ease;
    }
    .navbar-socials{
        display: none;
    }
    .navbar-icon-open{
        display: block;
        cursor: pointer;
        margin-right: 1rem;
        width: 25px;
        height: 25px;
    }
    .navbar-icon-close{
        background-color: white;
        border-radius: 50%;
    }
    .navbar-responsive{
        transition: all 0.5s ease;
    }

} 

